.homeBanner {
    width: 100%;
    height: 100vh;
    background-image: url("../Components/Imges/HomeBanner.png");
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: space-between;
    padding: 0 6%;

    .Home_col {
        padding-top: 100px;
        width: 50%;

        &:nth-of-type(1) {
            height: 100%;

            h3 {
                padding-top: 120px;
                font-family: "Poppins";
                font-size: 20px;
                line-height: 31px;
                color: #E87D0E;
            }

            h1 {
                padding-top: 15px;
                font-family: "Poppins";
                font-size: 50px;
                line-height: 64px;
                color: #fff;
            }

            p {
                padding-top: 25px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
            }

            button {
                margin-top: 50px;
                background-color: #FA8305;
                padding: 10px 25px;
                border: 2px solid #FA8305;
                outline: none;
                border-radius: 40px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
                transition: all .4s;

                &:hover {
                    color: #FA8305;
                    background-color: transparent;
                }

            }
        }

        &:nth-of-type(2) {
            position: relative;

            img {
                position: absolute;

                &:nth-of-type(1) {
                    top: 450px;
                    left: -80px;
                }

                &:nth-of-type(2) {
                    top: 150px;
                    left: 80px;
                }

                &:nth-of-type(3) {
                    top: 150px;
                    right: 100px;
                }
            }
        }


    }

}


.CardsGames {
    padding: 10px 8%;


    .CardsGames_row1 {
        display: flex;
        align-items: center;
        justify-content: space-between;

        h2 {
            font-family: "Poppins";
            font-size: 31px;
            line-height: 38px;
            color: #fff;



        }

        button {
            background-color: rgba(255, 255, 255, 0.11);
            border-radius: 8px;
            padding: 10px 15px;
            font-family: "Poppins";
            font-size: 24px;
            line-height: 38px;
            color: #fff;
            transition: all .4s;

            &:hover {
                color: #FA8305;
            }

        }
    }

    .CardsGames_row2 {
        padding-top: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 40px;

        .CardsGames_itme {
            border-radius: 16px;
            width: 300px;

            img {
                width: 100%;
                height: 100%;
                transition: all .4s;
            }

            p {
                padding: 15px 0;
                text-align: center;
                font-family: "Rubik";
                font-size: 22px;
                line-height: 28px;
                font-weight: 600;
                letter-spacing: 2px;
                color: #fff;
                transition: all .6s;

                svg {
                    path {
                        transition: all .4s;
                    }
                }



                span {
                    margin-right: 8px;
                }

            }

            &:hover {
                img {
                    transform: scale(1.09);

                }

                p {
                    color: #ffffff78;

                    svg {
                        path {
                            fill: #ffffff78;
                        }
                    }
                }
            }
        }
    }


}

.homeAdvertising {
    width: 100%;


    .homeAdvertising_row_1 {
        padding: 100px 6% 0;

        .homeAdvertising_row1_title {
            text-align: center;
            font-family: "Poppins";
            font-size: 35px;
            line-height: 66px;
            color: #fff;
        }

        .homeAdvertising_row1_info {
            .homeAdvertising_row1_info_title {
                padding-top: 65px;

                h3 {
                    font-family: "Poppins";
                    font-size: 30px;
                    line-height: 56px;
                    color: #fff;

                }

                p {
                    padding-top: 32px;
                    font-family: "Poppins";
                    font-size: 16px;
                    line-height: 28px;
                    color: #fff;

                }
            }

            .homeAdvertising_row1_info_img {
                padding-top: 50px;
                width: 100%;
                height: auto;

                img {
                    width: 100%;
                    height: auto;
                }
            }

        }
    }

    .homeAdvertising_row_2 {
        margin-top: 120px;
        position: relative;
        width: 100%;
        height: auto;
        padding: 0;
        z-index: 0;

        .box {
            width: 100%;
            height: 100%;
            padding: 20px 6%;
            background-color: #0000007b;

            .homeAdvertising_row_2_title {

                text-align: center;
                color: #fff;
                font-family: "Poppins";

                h2 {
                    padding: 50px 6% 0;
                    font-size: 30px;

                }

                p {
                    padding: 25px 6% 0;
                    font-size: 16px;
                }
            }

            .homeAdvertising_row_2_svg {
                padding-top: 70px;
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-wrap: wrap;
                gap: 40px;

                .homeAdvertising_row_2_svg_itmes {
                    width: 225px;
                    height: 160px;
                    border-radius: 12px;

                    span {

                        margin: 25px auto 0;
                        width: 65px;
                        height: 65px;
                        border-radius: 50%;
                        background-color: #fff;
                        border: 1px solid #fff;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        transition: all .4s;

                        svg {
                            path {
                                transition: all .4s;
                            }
                        }

                        &:hover {
                            background-color: #E87D0E;

                            svg {
                                path {
                                    fill: #fff;
                                }
                            }

                            &:hover+p {
                                color: #E87D0E;
                            }
                        }
                    }

                    p {
                        padding-top: 30px;
                        text-align: center;
                        font-family: "Poppins";
                        font-size: 16px;
                        line-height: 30px;
                        color: #fff;
                        transition: all .4s;
                    }
                }
            }
        }

        video {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: -1;
        }

    }

}

.CardsProjects {
    padding: 20px 6%;

    .CardsProjects_title {
        padding: 120px 0;
        text-align: center;
        font-family: "Poppins";
        color: #fff;

        h2 {
            font-size: 30px;
            line-height: 56px;
        }
    }

    .CardsProjects_card {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;
        row-gap: 40px;

        .img_row {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 30px;

            img {
                width: 100%;
                object-fit: cover;
                transition: all .4s;

                &:hover {
                    transform: scale(.9);
                }


            }
        }


    }

    button {
        margin: 82px auto 0;
        display: block;
        background-color: rgba(120, 120, 120, 0.658);
        padding: 10px 15px;
        font-family: "Poppins";
        font-size: 20px;
        line-height: 24p;
        border-radius: 8px;
        color: #fff;
        transition: all .4s;

        &:hover {
            color: #FA8305;
        }

    }
}

