.Userloop {
    width: 100%;
    padding: 100px 50px;
    height: auto;




    .Userloop_row_1 {
        h2 {
            font-family: "Poppins";
            font-size: 30px;
            line-height: 56px;
            color: #fff;
        }

        p {
            font-family: "Poppins";
            font-size: 16px;
            line-height: 24px;
            color: #fff;
        }
    }

    .Userloop_row_2 {
        margin-top: 62px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #1C140F;
        border-radius: 15px;
        padding: 50px 40px;

        .Userloop_row_2_col {
            &:nth-of-type(1) {
                h3 {
                    font-family: "Poppins";
                    font-size: 24px;
                    line-height: 56px;
                    color: #fff;
                    font-weight: 400;
                }

                p {
                    font-family: "Poppins";
                    font-size: 14px;
                    line-height: 24px;
                    color: #fff;

                }

            }

            &:nth-of-type(2) {
                .Userloop_row_2_col_input {
                    background-color: #fff;
                    width: 100%;
                    height: auto;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border-radius: 8px;
                    overflow: hidden;

                    input {
                        background-color: transparent;
                        width: 60%;
                        height: 100%;
                        padding: 20px;
                        font-size: 18px;
                        border: none;
                        outline: none;
                        font-family: "Poppins";
                        height: 20px;

                        &::placeholder {
                            font-size: 13px;
                            font-family: "Poppins";
                            height: 20px;
                            color: #898989;
                        }
                    }

                    button {
                        background-color: #DC7000;
                        margin: 0 10px;
                        padding: 6px 15px;
                        border-radius: 8px;
                        border: 2px solid #DC7000;
                        outline: none;
                        font-family: "Poppins";
                        font-size: 13px;
                        line-height: 20px;
                        color: #fff;
                        transition: all .6s;

                        &:hover {
                            background-color: transparent;
                            color: #DC7000;
                        }
                    }
                }

            }
        }




    }
}

@media screen and (max-width:1040px) {
    .Userloop {
        .Userloop_row_2 {
            .Userloop_row_2_col {
                width: 50%;

                &:nth-of-type(1) {
                    p {
                        padding-right: 10px;

                        br {
                            display: none;
                        }
                    }
                }
            }
        }
    }



}

@media screen and (max-width:760px) {
    .Userloop {
        .Userloop_row_2 {
            display: grid;
            gap: 40px;

            .Userloop_row_2_col {
                width: 100%;
            }
        }
    }





}

@media screen and (max-width:500px) {
    .Userloop {
        .Userloop_row_2 {
            .Userloop_row_2_col {
                &:nth-of-type(1) {
                    h3 {
                        text-align: center;
                    }

                    p {
                        text-align: center;
                        font-size: 14px;
                    }
                }

                &:nth-of-type(2) {
                    .Userloop_row_2_col_input {
                        background-color: #1C140F;
                        display: grid;
                        gap: 40px;

                        input {
                            background-color: #fff;
                            width: 100%;
                            border-radius: 10px;
                        }
                    }

                }


            }
        }
    }





}

@media screen and (max-width:400px) {
    .Userloop {
        .Userloop_row_2 {
            padding: 30px 20px;

            .Userloop_row_2_col {
                &:nth-of-type(1) {
                    h3 {
                        text-align: center;
                        font-size: 20px;
                    }

                    p {
                        text-align: center;
                        font-size: 12px;
                    }
                }

                &:nth-of-type(2) {
                    .Userloop_row_2_col_input {
                        background-color: #1C140F;
                        display: grid;
                        gap: 40px;

                        input {
                            background-color: #fff;
                            width: 100%;
                            border-radius: 10px;
                        }
                    }

                }


            }
        }
    }





}