@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.Navbar {
    width: 100%;
    backdrop-filter: blur(5px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 6%;
    position: fixed;
    top: 0;
    z-index: 1;

    .Navbar_Logo {
        a {
            text-decoration: none;
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 800;
            font-size: 29px;
            line-height: 153.5%;
            color: #FFFFFF;
        }
    }

    .Navbar_col {
        ul {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 60px;
            list-style: none;

            li {
                a {
                    text-decoration: none;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #FFFFFF;
                    transition: all .6s;
                    position: relative;

                    &::after {
                        position: absolute;
                        content: "";
                        width: 0;
                        height: 2px;
                        background-color: #FA9021;
                        left: 0;
                        bottom: -2px;
                        transition: all .6s;
                    }

                    &:hover {
                        color: #FA9021;

                        &::after {
                            width: 100%;
                        }
                    }
                }

                &:nth-last-of-type(1) {
                    padding: 10px 25px;
                    background: #FA9021;
                    border: 1.5px solid #FA9021;
                    border-radius: 6px;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 13px;
                    line-height: 20px;
                    color: #FFFFFF;
                    transition: all .6s;

                    a {
                        &::after {
                            display: none;
                        }
                    }

                    &:hover {
                        background-color: transparent;

                        a {
                            color: #FA9021;
                        }
                    }
                }
            }
        }

        .Navbar_col_mobil_menu {
            span {
                display: none;
                font-size: 3rem;
                color: #FFFFFF;
            }

            ul {
                background-color: #FA9021;
                display: none;
            }
        }
    }
}

@media screen and (max-width:785px) {
    .Navbar {
        .Navbar_col {
            ul {
                gap: 42px;

            }
        }

    }


}

@media screen and (max-width:700px) {
    .Navbar {
        .Navbar_col {
            ul {
                gap: 30px;

                li {
                    a {
                        font-size: 14px;
                    }
                }

            }
        }

    }


}

@media screen and (max-width:600px) {
    .Navbar {
        .Navbar_Logo {
            a {
                font-size: 26px;
            }
        }

        .Navbar_col {
            ul {
                gap: 28px;

                li {
                    a {
                        font-size: 14px;
                    }

                    &:nth-last-of-type(1) {
                        padding: 5px 15px;
                    }

                }
            }

        }


    }

}


@media screen and (max-width:560px) {
    .Navbar {

        .Navbar_Logo {
            a {
                font-size: 24px;
            }
        }

        .Navbar_col {
            ul {
                gap: 20px;

                li {
                    a {
                        font-size: 14px;
                    }

                    &:nth-last-of-type(1) {
                        padding: 5px 15px;
                    }

                }
            }

        }


    }

}

@media screen and (max-width:530px) {
    .Navbar {

        .Navbar_col {
            ul {
                gap: 15px;

                li {
                    a {
                        font-size: 14px;
                    }

                    &:nth-last-of-type(1) {
                        padding: 5px 15px;
                    }

                }
            }

        }


    }

}

@media screen and (max-width:500px) {
    .Navbar {
        .Navbar_Logo {
            a {
                font-size: 32px;
            }
        }

        .Navbar_col {
            ul {

                display: none;



            }

            .Navbar_col_mobil_menu {
                span{
                  
                    display: block;
                    transform: scale(1.6);
                }
                ul {
                    
                    display: block;
                    position: absolute;
                    background-color: #171717e3;
                    width: 100%;
                    height: auto;
                    left: 600px;
                    transition: all .4s;

                    li {
                        margin: 25px 0;
                        text-align: center;
                        padding: 20px 0;
                        position: relative;
                        z-index: 0;

                        a {
                            font-size: 3rem;

                            &::after {
                                display: none;
                            }
                        }

                        &::after {
                            position: absolute;
                            content: '';
                            width: 0;
                            height: 100%;
                            background-color: rgba(165, 165, 165, 0.562);
                            right: 0;
                            top: 0;
                            z-index: -1;
                            transition: all .4s;

                        }

                        background-color: transparent;
                        border: none;

                        &:hover {
                            &::after {
                                width: 100%;
                            }

                            a {
                                font-size: 3.2rem;
                            }
                        }


                    }

                }

                // span {
                //     display: block;

                //     &:hover {
                //         +ul {
                //             left: 0;
                //         }
                //     }
                // }
            }

        }
    }
}