@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

@mixin font {
    h2 {
        font-family: "Poppins";
        font-size: 30px;
        line-height: 45px;
        color: #fff;
    }

    p {
        padding-top: 42px;
        font-family: "Poppins";
        font-size: 16px;
        line-height: 28px;
        color: #fff;
    }

    button {
        margin-top: 52px;
        background-color: #DC7000;
        border: 1.5px solid #DC7000;
        outline: none;
        padding: 12px 42px;
        font-family: "Poppins";
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        transition: all .5s;
        cursor: pointer;

        &:hover {
            background-color: transparent;
            color: #DC7000;
        }

    }
}

.Statik {
    padding: 20px 6%;

    .Statik_row {
        background-color: #1C140F;
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 10px;

        .Statik_col {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 30%;
            height: 100%;
            display: flex;
            align-items: center;
            gap: 35px;
            position: relative;

            span {
                cursor: pointer;
                width: 55px;
                height: 55px;
                background-color: #fff;
                border: 1px solid #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                transition: all .4s;

                svg {
                    path {
                        transition: all .4s;

                    }
                }

                &:hover {
                    background-color: #DC7000;

                    svg {
                        path {
                            fill: #fff;
                        }
                    }

                    +.d {

                        h5,
                        p {
                            color: #DC7000;
                        }


                    }
                }
            }

            .d {
                h5 {
                    font-family: "Poppins";
                    font-size: 24px;
                    line-height: 30px;
                    color: #fff;
                    transition: all .4s;
                }

                p {
                    padding-top: 5px;
                    font-family: "Poppins";
                    font-size: 18px;
                    line-height: 30px;
                    color: #fff;
                    transition: all .4s;
                }
            }

            &:nth-of-type(1) {
                &::after {
                    position: absolute;
                    content: "";
                    width: 2px;
                    height: 80%;
                    background-color: #fff;
                    right: 0px;
                }

            }

            &:nth-of-type(2) {
                &::after {
                    position: absolute;
                    content: "";
                    width: 2px;
                    height: 80%;
                    background-color: #fff;
                    right: 0px;
                }

            }

        }

    }
}

.Advertising_1 {
    padding: 100px 6% 0;
    display: flex;
    justify-content: center;
    gap: 40px;

    .Advertising_1_col {
        &:nth-of-type(1) {
            img {
                object-fit: cover;
                width: 600px;
                height: 300px;
            }
        }

        &:nth-of-type(2) {
            h2 {
                font-family: "Poppins";
                font-size: 30px;
                line-height: 42px;
                color: #fff;
            }

            p {
                padding-top: 12px;
                font-family: "Poppins";
                font-size: 14px;
                line-height: 26px;
                color: #fff;
            }

            ul {
                padding-top: 28px;

                li {
                    display: flex;
                    align-items: start;
                    gap: 10px;
                    font-family: "Poppins";
                    font-size: 14px;
                    line-height: 26px;
                    color: #fff;
                }
            }
        }
    }
}

.Advertising_2 {
    padding: 120px 6% 0;
    display: flex;
    justify-content: space-between;
    gap: 60px;

    .Advertising_2_col {
        width: 50%;

        &:nth-of-type(1) {
            @include font();
        }

        &:nth-of-type(2) {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }


}

.Advertising_3 {
    padding: 120px 6% 0;
    display: flex;
    justify-content: space-between;
    gap: 60px;

    .Advertising_3_col {
