.Customer {
    margin-top: 120px;
    background-color: #1C140F;

    .Customer_title {
        padding-top: 80px;
        text-align: center;

        h2 {
            font-family: "Poppins";
            font-size: 30px;
            line-height: 45px;
            color: #fff;
        }

        p {
            padding-top: 20px;
            font-family: "Poppins";
            font-size: 16px;
            line-height: 28px;
            color: #fff;
        }
    }

    .Customer_row {
        padding: 50px 6% 0;

        .slick-slide>div {
            margin: 0 30px;
        }

        .slick-list {
            margin: 0 -30px;
        }
        .slick-next:before {
            display: none;
        }
        .slick-prev:before {
            display: none;
        }
        

        .Customer_row_item {
            width: 30px;
            height: 230px;
            border: 2px solid darkgoldenrod;
            border-radius: 10px;

            .Customer_row_item_row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 20px 30px;

                .Customer_row_item_row_name {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    column-gap: 20px;

                    img {
                        object-fit: cover;
                    }

                    span {
                        h6 {
                            font-family: "Poppins";
                            font-size: 16px;
                            line-height: 30px;
                            color: #fff;
                        }

                        p {
                            font-family: "Poppins";
                            font-size: 14px;
                            line-height: 24px;
                            color: #4f5665a7;
                        }
                    }

                }

                p {
                    display: flex;
                    align-items: center;
                    font-family: "Poppins";
                    text-align: center;
                    gap: 10px;
                    // font-size: 1px;
                    line-height: 24px;
                    color: #FFFFFF;

                    span {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }


            }

            #p {
                padding: 0 15px;
                font-family: "Poppins";
                font-size: 14px;
                line-height: 24px;
                color: #FFFFFF;


            }

        }
    }

    .Customer_row_2 {
        padding: 60px 6%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .Customer_row_right {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 20px;

            span {
                width: 60px;
                height: 60px;
                border: 1.5px solid #DC7000;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all .6s;

                svg{
                    path{
                        transition: all .4;
                        fill: #fff;
                    }
                }

                &:hover{
                    background-color: #4f4e4c5e;
                    border: none;
                    svg{
                        path{
                            fill: #DC7000;
                        }
                    }
                }

            }

        }
    }


}

