.Footer {
    background-color: #070707;

    .Footer_row_1 {
        padding: 30px 6%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        .Footer_col {
            padding: 2px;

            &:nth-of-type(1) {

                h2 {
                    font-family: "Poppins";
                    font-size: 24px;
                    line-height: 37px;
                    color: #fff;
                }

                p {
                    font-family: "Poppins";
                    font-size: 14px;
                    line-height: 24px;
                    color: #fff;
                }

                h6 {
                    padding-top: 25px;
                    font-family: "Poppins";
                    font-size: 16px;
                    line-height: 24px;
                    font-weight: 100;
                    color: #fff;
                }
            }

            &:nth-of-type(2) {
                padding-left: 40px;

                h3 {
                    font-family: "Poppins";
                    font-size: 17px;
                    line-height: 37px;
                    font-weight: 600;
                    color: #fff;
                }

                ul {
                    padding: 0;
                    margin: 0;
                    list-style-type: none;

                    li {
                        a {
                            text-align: center;
                            text-decoration: none;
                            font-family: "Poppins";
                            font-size: 16px;
                            line-height: 37px;
                            font-weight: 400;
                            color: #fff;
                            transition: all .4s;
                            position: relative;

                            &::after {
                                position: absolute;
                                content: "";
                                width: 0;
                                height: 2px;
                                background-color: #c5c5c5;
                                left: 0;
                                bottom: -5px;
                                transition: all .4s;
                            }

                            &:hover {
                                color: #c5c5c5;

                                &::after {
                                    width: 100%;
                                }


                            }
                        }

                    }
                }
            }

            &:nth-of-type(3) {

                h3 {
                    font-family: "Poppins";
                    font-size: 17px;
                    line-height: 37px;
                    color: #fff;
                }

                p {
                    font-family: "Poppins";
                    font-size: 14px;
                    line-height: 24px;
                    color: #fff;
                }

                h6 {
                    padding-top: 12px;
                    font-family: "Poppins";
                    font-size: 16px;
                    line-height: 24px;
                    font-weight: 100;
                    color: #fff;
                }
            }

            &:nth-of-type(4) {
                display: flex;
                align-items: flex-end;
                justify-content: center;
                column-gap: 16px;

                span {
                    background-color: #fff;
                    border: 1px solid #fff;
                    width: 48px;
                    height: 48px;
                    padding: 10px;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .5s;

                    svg {
                        transition: all .5s;
                    }

                    &:hover {
                        background-color: transparent;

                        svg {
                            path {
                                fill: #fff;
                            }
                        }

                        box-shadow: 0 0 10px #fff,
                        0 0 10px #fff;
                    }




                }
            }

        }
    }

    .Footer_row_2 {
        border-top: 1.5px solid #fff;
        text-align: center;
        padding: 25px 0;
        font-family: "Poppins";
        font-size: 16px;
        color: #c5c5c5;
    }
}


@media screen and (max-width:790px) {
    .Footer {
        .Footer_row_1 {
            .Footer_col {

                &:nth-of-type(1),
                &:nth-of-type(3) {
                    p {
                        font-size: 12px;
                    }
                }

                &:nth-of-type(4) {
                    span {
                        width: 32px;
                        height: 32px;
                    }
                }
            }
        }
    }


}


@media screen and (max-width:790px) {
    .Footer {
        .Footer_row_1 {
            grid-template-columns: repeat(2, 1fr);

            .Footer_col {

                &:nth-of-type(1),
                &:nth-of-type(3) {
                    p {
                        font-size: 16px;
                    }
                }

                &:nth-of-type(2) {
                    padding-left: 40px;

                    h3 {
                        font-size: 28px;

                    }

                    ul {

                        li {
                            a {
                                font-size: 24px;

                            }
                        }
                    }
                }

                &:nth-of-type(4) {
                    justify-content: flex-end;

                    span {

                        width: 40px;
                        height: 40px;
                    }
                }
            }
        }



    }


}

@media screen and (max-width:500px) {
    .Footer {
        .Footer_row_1 {
            grid-template-columns: 1fr;
            row-gap: 40px;

            .Footer_col {

                &:nth-of-type(1),
                &:nth-of-type(3) {

                    h2,
                    h3 {
                        font-size: 28px;
                    }


                    p {
                        font-size: 18px;
                    }
                }

                &:nth-of-type(2) {
                    padding: 0;

                    h3 {
                        font-size: 28px;

                    }

                    ul {

                        li {
                            a {
                                font-size: 24px;

                            }
                        }
                    }
                }

                &:nth-of-type(4) {
                    justify-content: center;

                    span {

                        width: 40px;
                        height: 40px;
                    }
                }
            }
        }
    }


}