.News {
    padding: 10px 5%;

    #textNews {

        background-color: #1C140F;
        outline: none;
        border: none;
        border-radius: 10px;
        display: block;
        margin: 65px auto 0;
        padding: 15px 220px 10px 40px;

        &::placeholder {
            background-color: transparent;
            font-family: "Poppins";
            font-size: 18px;
            line-height: 30px;
        }

    }

    button {
        cursor: pointer;
    }

    .News_row {
        padding: 100px 0 0;
        display: flex;
        justify-content: space-between;
        gap: 72px;

        .News_col {
            width: 50%;

            &:nth-of-type(1) {
                display: grid;
                grid-template-columns: 1fr;
                row-gap: 80px;

                .News_col_row {
                    position: relative;

                    img {
                        width: 100%;
                        height: 250px;
                        object-fit: cover;
                    }

                    .News_col_row_btn {
                        padding-top: 20px;
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        font-family: "Poppins";
                        font-size: 16px;
                        line-height: 30px;
                        color: #FFFCFC;

                        button {
                            padding: 6px 25px;
                            font-family: "Poppins";
                            font-size: 16px;
                            line-height: 30px;
                            color: #FFFCFC;
                            background-color: #2351F5;
                            border: 1.5px solid #2351F5;
                            outline: none;
                            transition: all .4s;

                            &:hover {
                                color: #2351F5;
                                background-color: #2351F515;
                            }
                        }
                    }

                    h2 {
                        padding-top: 20px;
                        font-family: "Poppins";
                        font-size: 30px;
                        line-height: 45px;
                        color: #fff;
                    }

                    p {
                        padding: 10px;
                        font-family: "Poppins";
                        font-size: 16px;
                        line-height: 28px;
                        color: #fff;
                    }

                    &:nth-of-type(1) {
                        &::after {
                            position: absolute;
                            content: "";
                            width: 100%;
                            height: 2px;
                            background-color: #a5a5a598;
                            bottom: -30px;
                            left: 0;
                        }
                    }
                }


            }

            &:nth-of-type(2) {
                display: grid;
                grid-template-columns: 1fr;
                row-gap: 20px;

                .News_col_row_item {
                    width: 100%;
                    height: 180px;
                    display: flex;
                    gap: 30px;

                    img {
                        width: 180px;
                        height: 100%;
                    }

                    .News_col_row_item_col {
                        .News_col_row_item_btn {
                            padding-top: 15px;
                            display: flex;
                            align-items: center;
                            gap: 20px;
                            font-family: "Poppins";
                            font-size: 16px;
                            line-height: 30px;
                            color: #FFFCFC;

                            button {
                                padding: 6px 25px;
                                font-family: "Poppins";
                                font-size: 16px;
                                line-height: 30px;
                                color: #FFFCFC;
                                border: 1.5px solid;
                                outline: none;
                                transition: all .4s;
                            }
                        }
                    }

                    h3 {
                        padding-top: 25px;
                        font-family: "Poppins";
                        font-size: 18px;
                        line-height: 24px;
                        color: #fff;
                    }

                    &:nth-of-type(1) {
                        button {
                            background-color: #FF8484;

                            &:hover {
                                color: #FF8484;
                                background-color: #FF848415;
                            }
                        }
                    }

                    &:nth-of-type(2) {

                        button {
                            background-color: #2351F5;

                            &:hover {
                                color: #2351F5;
                                background-color: #2351F515;
                            }
                        }
                    }

                    &:nth-of-type(3) {

                        button {
                            background-color: #FF7C32;

                            &:hover {
                                color: #FF7C32;
                                background-color: #FF7C3215;
                            }
                        }
                    }

                    &:nth-of-type(4) {

                        button {
                            background-color: #2351F5;

                            &:hover {
                                color: #2351F5;
                                background-color: #2351F515;
                            }

                        }
                    }

                    &:nth-of-type(5) {

                        button {
                            background-color: #23A54F;

                            &:hover {
                                color: #23A54F;
                                background-color: #23A54F15;
                            }
                        }
                    }

                    &:nth-of-type(6) {

                        button {
                            background-color: #2351F5;

                            &:hover {
                                color: #2351F5;
                                background-color: #2351F515;
                            }

                        }
                    }

                    &:nth-of-type(7) {

                        button {
                            background-color: #9921C3;

                            &:hover {
                                color: #9921C3;
                                background-color: #9921C315;
                            }
                        }
                    }

                }




            }
        }
    }

}

