.Titletext {
    padding-top: 100px;
    text-align: center;

    h5 {
        font-family: "Poppins";
        font-size: 16px;
        line-height: 33px;
        color: #fff;

        span {
            color: #E47500;


        }
    }

    h2 {
        padding-top: 20px;
        font-family: "Poppins";
        font-size: 32px;
        line-height: 46px;
        color: #fff;
    }

    p {
        padding: 10px 0;
        font-family: "Poppins";
        font-size: 16px;
        line-height: 30px;
        color: #fff;
    }
}

@media screen and (max-width:890px) {
    .Titletext {
        h2 {
            padding: 0 30px;
            font-size: 28px;
        }
    }


}

@media screen and (max-width:450px) {
    .Titletext {
        h2 {
            padding: 0 30px;
            font-size: 26px;
            line-height: 36px;
        }

        p {
            padding: 0 10px;
            font-size: 14px;
        }
    }


}