@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";



.AboutBanner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .AboutBanner_col {
        width: 50%;
        height: 100%;


        &:nth-of-type(1) {
            padding: 40px 0 0 6%;

            h3 {
                font-family: "Poppins";
                font-size: 12px;
                line-height: 25px;
                color: #fff;
            }

            h1 {
                padding-top: 30px;
                font-family: "Poppins";
                font-size: 30px;
                line-height: 50px;
                color: #fff;
            }

            p {
                padding-top: 10px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
            }

            button {
                margin-top: 30px;
                outline: none;
                border: none;
                padding: 10px 25px;
                background-color: #FA8305;
                border: 2px solid #FA8305;
                border-radius: 40px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
                transition: all .4s;

                span {
                    margin-left: 18px;

                    svg {
                        path {
                            transition: all .4s;
                        }
                    }
                }

                &:hover {
                    background-color: transparent;
                    color: #FA8305;

                    span {
                        svg {
                            path {
                                fill: #FA8305;
                            }
                        }
                    }

                }
            }
        }

        &:nth-of-type(2) {
           img{
            width: 100%;
            height: 100%;
            object-fit: cover;
           }
           


        }

    }
}

.CardsWork {
    width: 100%;
    height: auto;
    padding: 30px 6%;
    cursor: pointer;

    .CardsWork_title {
        padding: 130px 6% 60px;
        font-family: "Poppins";
        font-size: 25px;
        line-height: 45px;
        color: #fff;
        font-weight: 600;
    }

    .CardsWork_card {
        width: 100%;
        height: auto;

        .slick-slide>div {
            margin: 0 30px;
        }

        .slick-list {
            margin: 0 -30px;
        }

        .CardsWork_itmes {
            width: 350px;
            height: 340px;
            background-color: #1C140F;
            border-radius: 20px;
            padding: 64px 32px;

            button {
                cursor: pointer;
                background-color: #283149;
                font-family: "Poppins";
                font-size: 14px;
                line-height: 26px;
                color: #fff;
                padding: 10px 25px;
                border-radius: 8px;
                border: none;
                outline: none;
                transition: all .4s;
                border: 1.5px solid #283149;

                &:hover {
                    color: #FA8305;
                    border-color: #FA8305;
                    background-color: transparent;
                }

            }

            h3 {
                padding-top: 20px;
                font-family: "Poppins";
                font-size: 20px;
                line-height: 38px;
                color: #fff;
            }

            p {
                padding-top: 12px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
            }

        }

    }

}

.AboutAdvertising {
    margin-top: 120px;
    padding: 0 6%;
    display: flex;
    justify-content: space-between;
    background-color: #1C140F;

    .AboutAdvertising_col {
        width: 50%;

        &:nth-of-type(1) {
            padding: 60px 40px 0;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &:nth-of-type(2) {
            padding: 100px 4% 0 1px;

            h6 {
                font-family: "Poppins";
                font-size: 14px;
                line-height: 32px;
                font-weight: 400;
                color: #fff;
            }

            h3 {
                padding-top: 15px;
                font-family: "Poppins";
                font-size: 28px;
                line-height: 42px;
                color: #fff;
            }

            p {
                padding-top: 12px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
            }
        }
    }

}

.CardsTeam {
    padding: 0 6%;

    .CardsTeam_title {
        padding-top: 120px;
        font-family: "Poppins";
        font-size: 25px;
        line-height: 45px;
        color: #fff;
    }

    .CardsTeam_card {
        margin-top: 50px;
        height: auto;
        cursor: pointer;

        .slick-slide>div {
            margin: 0 10px;
        }

        .slick-list {
            margin: 0 -10px;
        }

        .CardsTeam_card_item {
            width: 300px;
            height: 250px;
            border-radius: 20px;
            background-color: #1C140F;

            img {

                margin: 30px auto 0;
            }

            h6 {
                text-align: center;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 19px;
                line-height: 202.5%;
                color: #fff;
            }

            p {
                text-align: center;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 202.5%;
                color: #E8E8E8;
            }

        }


    }
}

@media screen and (max-width:1315px) {
    .AboutAdvertising {
        .AboutAdvertising_col {
            &:nth-of-type(2) {
                h3 {
                    font-size: 26px;
                    line-height: 36px;
                }

                p {
                    font-size: 14px;
                }
            }
        }

    }
}

