@media screen and (max-width:740px) {
    .AboutBanner {
        height: 500px;

        .AboutBanner_col {

            &:nth-of-type(1) {
                h3 {
                    padding-top: 40px;
                }

                h1 {
                    padding: 0 5px;
                    font-size: 20px;
                    line-height: 32px;

                }

                p {
                    font-size: 12px;


                }
            }
        }
    }

    .CardsWork {
        .CardsWork_card {
            .CardsWork_itmes {
                padding: 32px 25px;

            }
        }

    }


}

@media screen and (max-width:640px) {
    .CardsWork {
        .CardsWork_card {
            .slick-next:before {
                display: none;

            }

            .slick-prev:before {
                display: none;
            }
        }

    }

}

@media screen and (max-width:600px) {
    .AboutBanner {
        height: auto;
        display: block;

        .AboutBanner_col {
            width: 100%;

            &:nth-of-type(1) {
                h3 {
                    padding-top: 40px;
                    font-size: 14px;
                }

                h1 {
                    padding-top: 30px;
                    text-align: center;
                    font-size: 32px;
                    line-height: 42px;

                    br {
                        display: block;
                    }

                }

                p {
                    text-align: center;
                    font-size: 16px;


                }

                button {
                    margin: 50px 6%;
                    float: right;

                }
            }
        }
    }

    .AboutAdvertising {
        display: block;

        .AboutAdvertising_col {
            width: 100%;

            &:nth-of-type(1) {
                padding: 0;

                img {
                    object-fit: cover;
                }
            }

            &:nth-of-type(2) {
                padding-top: 40px;

                h3 {
                    text-align: center;
                    font-size: 24px;
                    line-height: 32px;

                    br {
                        display: none;
                    }
                }

                p {
                    font-size: 12px;
                }

            }
        }

    }


}


