.Contact {
    .Contact_c {
     padding: 50px 6% 100px;
        .Contact_c_row {
            .Contact_c_row_cart {

                width: 100%;
                height: 100%;
                object-fit: cover;

                svg {
                    width: 100%;
                    height: 100%;

                    path {
                        transition: all .1s;

                        &:hover {
                            fill: #7f7b7b;
                        }
                    }



                }


            }


        }
    }








}



.Contact_log {
    background-color: #1C140F;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 4%;

    .Contact_log_box {
        width: 700px;

        .Contact_log_title {
            text-align: center;

            h3 {
                font-family: "Poppins";
                font-size: 30px;
                line-height: 57px;
                color: #fff;
            }

            p {
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                color: #fff;
            }


        }

        .Contact_log_input {
            label {
                padding-top: 20px;
                display: block;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 25px;
                color: #4F4F4F;
            }

            .Contact_log_input_row {

                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 80px;


            }

            input {
                background-color: #cecece27;
                width: 100%;
                font-family: "Poppins";
                font-size: 2rem;
                line-height: 24px;
                padding: 4px 20px;
                border: none;
                outline: none;
                border-radius: 10px;
            }

            textarea {
                background-color: #cecece27;
                width: 100%;
                height: 230px;
                font-family: "Poppins";
                font-size: 2rem;
                line-height: 24px;
                padding: 4px 20px;
                border: none;
                outline: none;
                border-radius: 10px;
            }

            button {
                margin-top: 50px;
                background-color: #DC7000;
                padding: 10px 42px;
                font-family: "Poppins";
                font-size: 16px;
                line-height: 24px;
                border: 1px solid #DC7000;
                outline: none;
                border-radius: 10px;
                float: right;
                color: #fff;
                transition: all .6s;

                &:hover {
                    background-color: transparent;
                    color: #DC7000;
                }
            }

        }
    }

}

@media screen and (max-width:500px) {
    .Contact_log{
        .Contact_log_box{
            .Contact_log_input{
                .Contact_log_input_row{
                    display: block;
                }

            }

        }
    }
    
}












